<template>
  <div class="platform">
    <tab :data="tabList" @change="changeTab" :current="tabIndex"></tab>
    <div class="content">
      <transition :enter-active-class="routerTransitionClass.enter"
                      :leave-active-class="routerTransitionClass.leave">
        <component :is="is"></component>
      </transition>
    </div>
  </div>
</template>

<script>
import tab from '@/components/common-tab'
import RouterTransition from '@/mixins/router-transition'
import performanceAnalysis from './components/performanceAnalysis/index.vue'

export default {
  name: 'platform',
  components: {
    tab,
    performanceAnalysis
  },
  mixins: [RouterTransition],
  data () {
    return {
      tabList: [
        {
          id: 1,
          name: '性能分析',
          com: performanceAnalysis
        }
        // {
        //   id: 2,
        //   name: '耐久分析',
        //   com: deviceModelManage
        // }
      ],
      tabIndex: 0,
      is: null
    }
  },
  created () {
    this.is = this.tabList[this.tabIndex].com
  },
  methods: {
    changeTab (index) {
      console.log(index)
      this.tabIndex = index
      this.is = this.tabList[index].com
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../style/varient';
@size-padding: 10px;
.platform{
  position: relative;
  width: 100%;
  height: 100%;
  .content{
    width: 100%;
    height: calc(~"100% - " 40px);
    // background-color: #fff;
  }
}
</style>



