<template>
  <div class="list">
    <div class="content">
      <!-- <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">性能分析</div>
          <span class="empty"></span>
        </div>
      </div> -->
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-input mr-12">
            <Select v-model="search.parentDeviceName"
              placeholder="请选择机组名称"
              clearable
            >
              <Option
                v-for="item in zhengji"
                :key="item.value"
                :value="item.value"
              >
                {{ item.name }}
              </Option>
            </Select>
          </div>

          <div class="filter-select mr-12">
            <StatusSelect
              placeholder="请选择单机组名称"
              :filterable="true"
              :type="4"
              v-model="search.deviceName"
            ></StatusSelect>
          </div>
          <div class="filter-input mr-12">
             <Select v-model="search.childSystemName"
              placeholder="请选择子系统名称" clearable>
              <Option
                v-for="item in childSystemName"
                :key="item.value"
                :value="item.value"
              >
                {{ item.name }}
              </Option>
            </Select>
          </div>
          <div class="filter-btn">
            <Button @click="loadTableData" class="search-btn" type="default">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
        <div class="filter-search-right">
          <span class="add-btn">
            <Button type="default" @click="showDrawer('add')">
              <!-- <span class="iconfont icon-a-lianhe68"></span> -->
              分析
            </Button>
          </span>
        </div>
      </div>
      <div class="user-table">
        <Table
          class="m-table no-scroll no-border"
          stripe
          :loading="pageInfo.loading"
          :columns="tableColumns"
          :row-class-name="rowClassName"
          @on-row-click="onRowClick"
          :data="tableData"
          style="min-height:600px"
        ></Table>
      </div>
      <div class="pagination">
        <Page
          @on-change="onPaginationChange"
          @on-page-size-change="onPaginationSizeChange"
          :current="pageInfo.pageIndex"
          :page-size="pageInfo.pageSize"
          :total="pageInfo.totalElements"
          show-elevator
          show-sizer
          show-total
        />
      </div>
      <custom-drawer
        ref="drawer"
        :title="title"
        :formValidate="formValidate"
        :formList="formList"
        v-model="formValue"
        @selectTree="selectTree"
        @getlocation="getlocation"
        :selectLocation="selectLocation"
        @confirm="confirm"
      ></custom-drawer>
    </div>
  </div>
</template>

<script>
import StatusSelect from '@/components/device-select'
import LaboratoryList from '@/components/laboratory-list'
import customDrawer from '@/components/common-drawer/common-drawer'
import { getDevicePerformance, toAnalysis, getchildSystemName, getDeviceSearchList } from '@/api/device'
export default {
  components: {
    StatusSelect,
    LaboratoryList,
    customDrawer
  },
  data () {
    return {
      childSystemName: [],
      zhengji: [{ name: 'G01', value: 'G01' }],
      search: {
        parentDeviceName: '',
        deviceName: '',
        childSystemName: ''
      },
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      tableData: [],
      maxlength: 50,
      selectLocation: '',
      tableColumns: [
        {
          title: '整机组名称',
          key: 'parentDeviceName',
          align: 'left'
        },
        {
          title: '单机组名称',
          key: 'deviceName',
          align: 'left'
        },
        {
          title: '子系统名称',
          key: 'childSystemName',
          align: 'left'
        },
        {
          title: '作图程序开始时间',
          key: 'drawStartTime',
          align: 'left'
        },
        {
          title: '作图程序结束时间',
          key: 'drawEndTime',
          align: 'left'
        },
        {
          title: '操作',
          key: 'action',
          width: 120,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    marginLeft: 0,
                    color: '#0084FF'
                  },
                  on: {
                    click: () => {
                      this.goDetail(params.row)
                    }
                  }
                },
                '下载'
              )
            ])
          }
        }
      ],
      currentIndex: 0,
      title: '',
      formList: [
        {
          label: '整机组名称',
          type: 'select',
          value: 'parentDeviceName',
          data: [{ name: 'G01', value: 'G01' }]
        },
        {
          label: '单机组名称',
          type: 'select',
          value: 'deviceName',
          data: []
        },
        {
          label: '子系统名称',
          type: 'select',
          value: 'childSystemName',
          data: []
        },
        {
          label: '开始时间',
          type: 'datePicker',
          value: 'drawStartTime'
        },
        {
          label: '结束时间',
          type: 'datePicker',
          value: 'drawEndTime'
        }
      ],
      formValue: {},
      formValidate: {
        deviceName: [{ required: true, message: '单机组名称不能为空' }],
        parentDeviceName: [{ required: true, message: '整机组名称不能为空' }],
        childSystemName: [{ required: true, message: '子系统名称不能为空' }],
        drawStartTime: [{ required: true, message: '开始时间不能为空' }],
        drawEndTime: [{ required: true, message: '结束时间不能为空' }]
      },
      type: ''
    }
  },
  methods: {
    selectTree (e) {
      this.formValue.addr = e
    },
    getlocation (e) {
      this.formValue.longitude = e.lng
      this.formValue.latitude = e.lat
    },
    getType () {
      getchildSystemName().then(res => {
        if (res.code === 200) {
          for (let item of res.data) {
            this.childSystemName.push({name: item, value: item})
          }
          this.formList[2].data = this.childSystemName
        }
      })
      getDeviceSearchList().then(res => {
        if (res.code === 200) {
          this.formList[1].data = res.data.map(i => {
            return {
              name: i.deviceName,
              value: i.id
            }
          })
        }
      })
    },
    loadTableData () {
      let { pageSize, pageIndex: pageNumber } = this.pageInfo
      this.pageInfo.loading = true
      getDevicePerformance({ pageSize, pageNumber, ...this.search }).then(
        res => {
          let { code, data } = res
          this.pageInfo.loading = false
          if (code === 200) {
            this.tableData = data.result
            this.pageInfo.totalElements = data.totalElements
          }
        }
      )
    },
    goDetail (params) {
      window.open(params.downloadUrl, '_blank')
    },
    showDrawer (type, params) {
      if (type !== 'delete') {
        if (type === 'add') {
          this.title = '作图程序条件'
          this.formValue = {}
        }
        this.type = type
        this.$refs.drawer.open()
      }
    },
    onRowClick (e) {},
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      }
    },
    fmtDate (dt) {
      let year = dt.getFullYear()
      let month = (dt.getMonth() + 1).toString().padStart(2, '0')
      let date = dt.getDate().toString().padStart(2, '0')
      let hour = dt.getHours().toString().padStart(2, '0')
      let minute = dt.getMinutes().toString().padStart(2, '0')
      let second = dt.getSeconds().toString().padStart(2, '0')
      return `${year}-${month}-${date} ${hour}:${minute}:${second}`
    },
    confirm () {
      console.log(this.formValue.drawStartTime)
      this.formValue.drawStartTime = this.fmtDate(this.formValue.drawStartTime)
      this.formValue.drawEndTime = this.fmtDate(this.formValue.drawEndTime)
      toAnalysis({ ...this.formValue }).then(res => {
        let { code, msg } = res
        if (code === 200) {
          this.$Message.success(res.msg)
          this.loadTableData()
          this.getType()
          this.$refs.drawer.close()
        } else {
          this.$Message.warning(msg)
        }
      })
    }
  },
  mounted () {
    this.loadTableData()
    this.getType()
  }
}
</script>

<style lang="less" scoped>
@import "../../../../style/varient.less";
@size-padding: 10px;

.ivu-date-picker {
  input {
    width: 240px !important;
  }
}
/deep/.ivu-page-options-elevator input {
  padding-left: 0.1rem !important;
  text-align: center;
}
.list {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}
</style>



